import React from 'react';
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/styles";
import { Card } from "@material-ui/core";
import CardContent from "@material-ui/core/CardContent";
import CardActionArea from "@material-ui/core/CardActionArea";
import DisabledSesh from "./DisabledSesh";
import Divider from "@material-ui/core/Divider";
import Chip from "@material-ui/core/Chip";
import Room from "@material-ui/icons/RoomOutlined";
import AvTimer from "@material-ui/icons/AvTimerOutlined";

const timeTable = {
  "S1": "08:30 - 10:00",
  "S2": "10:10 - 11:40",
  "S3": "11:50 - 13:20",
  "S4": "13:50 - 15:20",
  "S4'": "13:30 - 15:00",
  "S5": "15:30 - 17:00",
  "S6": "17:10 - 18:40"
}
const colorMap = {
  "TP": {
    background: "#EDEAFF",
    text: "#30005D",
    darker: "#705BCF"
  },
  "TD": {
    background: "#D9FFE5",
    text: "#30005D",
    darker: "#30005D"
  },
  "C": {
    background: "#FFE5E7",
    text: "#30005D",
    darker: "#30005D"

  }
}
const subjectNameExtractor = /-(.*?)-/;
const useStyles = makeStyles(theme => ({
  root: props => ({
    marginLeft: theme.spacing(2),
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    height: 200,
    backgroundColor: props.type && colorMap[props.type].background
  }),
  sessionRoot: {
    display: "flex",
  },
  subjectText: props => ({
    flex: 1,
    color: props.type && colorMap[props.type].text,
    fontSize: theme.typography.pxToRem(18),
    fontFamily: "Roboto Mono",
  }),
  timeText: props => ({
    color: props.type && colorMap[props.type].text,
    fontFamily: "monospace",
    textWrap: "nowrap",
  }),
  chip: props => ({
    backgroundColor: "white",
    color: props.type ? colorMap[props.type].text : "black",
    borderRadius: theme.shape.borderRadius,
    marginRight: theme.spacing(2),
    marginTop: theme.spacing(2)
  }),
  icons: {
    display: "flex",
    width: "100%"
  },
  iconGroup: {
    marginRight: theme.spacing(4),
    display: "flex",
    alignItems: "center"
  },
  icon: props => ({
    marginRight: theme.spacing(1),
    // fontSize: theme.typography.pxToRem(16),
    fontSize: theme.typography.pxToRem(18),
    color: props.type && colorMap[props.type].darker,

  }),
  iconText: props => ({
    color: props.type && colorMap[props.type].darker,
    fontFamily: "Roboto Mono",
    fontSize: theme.typography.pxToRem(18)
  })
}));


function Sesh(props) {
  const { subject, time, professor, room, regime, type, disabled, onClick } = props;
  let rawSubjectName = null;
  if (!disabled) {
    rawSubjectName = subjectNameExtractor.exec(subject)[1];
    rawSubjectName = rawSubjectName.replace(/(\(.*\))/, "")
  }
  const classes = useStyles(props);
  return (
    disabled ?
      <DisabledSesh onClick={onClick} {...props} /> :
      <React.Fragment>
        <Card className={classes.root} elevation={0}>
          <CardActionArea style={{ height: "100%" }}>
            <CardContent onClick={onClick} style={{ height: "100%", display: "flex", flexDirection: "column", justifyContent: "space-between" }}>
              <div className={classes.sessionRoot}>
                <Typography className={classes.subjectText} variant="h5">{rawSubjectName}</Typography>
                <Typography className={classes.timeText} variant="caption">{timeTable[time]}</Typography>
              </div>

              <div className={classes.icons}>
                <div className={classes.iconGroup} >
                  <Room className={classes.icon} />
                  <Typography className={classes.iconText} variant="subtitle1"  >{room}</Typography>
                </div>
                <div className={classes.iconGroup} >
                  <AvTimer className={classes.icon} />
                  <Typography className={classes.iconText} variant="subtitle1"  >{regime}</Typography>
                </div>
              </div>

              <div style={{ display: "flex" }}>
                <Chip label={type} className={classes.chip} />
                <Chip label={professor} className={classes.chip} />
                <Chip label={time} className={classes.chip} />
              </div>
            </CardContent>
          </CardActionArea>
        </Card>
        <Divider />
      </React.Fragment >
  );
}

export default Sesh;
