import React from "react";
import { makeStyles, Card, CardActionArea, CardContent, Typography } from "@material-ui/core";
import pattern from "./pattern.png"


const useStyles = makeStyles(theme => ({
  container: {
    margin: theme.spacing(2),

  },
  root: {
    height: 100,
    background: `url(${pattern})`
  },
  label: {
    fontFamily: "Roboto Mono",
    color: "#BBB",
    display: "inline-block",
    padding: theme.spacing(2)
  }
}));

export default function DisabledSesh(props) {
  const classes = useStyles();
  return <Card className={classes.container} elevation={0}>
    <CardActionArea onClick={props.onClick}>
      <CardContent className={classes.root}>
        <Typography className={classes.label} color="inherit" variant="subtitle1">Empty session: {props.time}</Typography>
      </CardContent>
    </CardActionArea>
  </Card>
}
