import React, { useState, useEffect } from 'react';
import Typography from "@material-ui/core/Typography";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import SwipeableViews from "react-swipeable-views";
import Sesh from "./Sesh";
import uuid from "uuid/v4";
import Alternatives from "./Alternatives";
import EditIcon from "@material-ui/icons/EditOutlined"
import IconButton from "@material-ui/core/IconButton";
import { withRouter } from "react-router-dom";
const WEEK_DAYS = ["lundi", "mardi", "mercredi", "jeudi", "vendredi", "samedi"];
const DAY_SESSIONS = ["S1", "S2", "S3", "S4", "S5", "S6"];

function splitSessions(sessions, subGroup = 1) {
    if (sessions && sessions.length !== 0) {
        const array = [];
        for (let i = 1; i <= 6; i++) {
            let subArray = sessions.filter(item => item.day === i && item.subGroup === subGroup);
            const timeMap = subArray.reduce((acc, next) => {
                if (!acc[next.time])
                    acc[next.time] = true
                return acc;
            }, {});

            DAY_SESSIONS.forEach(time => {
                if (i === 6 && time === "S4")
                    time = "S4'";
                if (!(time in timeMap))
                    subArray.push({ disabled: true, time: time, id: uuid() })
            });

            subArray.sort((a, b) => a.time > b.time ? 1 : -1);
            array.push(subArray);
        }
        return array;
    }
}


function Schedule(props) {
    const [data, setData] = useState([]);
    const [currentTab, setCurrentTab] = useState(0);
    const handleTabChange = (e, newValue) => setCurrentTab(newValue);
    const [alternatives, setAlternatives] = useState(null);
    console.log(props);
    const goBack = () => {
        window.localStorage.clear();
        props.history.push("/");
    }
    useEffect(() => {
        const _data = splitSessions(props.Sessions);
        setData(_data);
    }, [props.Sessions]);


    const fetchAlternatives = async (body = { majorName: "", year: "", day: "", time: "" }) => {
        console.log("requesting with params", body);
        await fetch("/api/sessions/alternatives", {
            method: "POST",
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
            .then(res => res.json())
            .then(data => {
                setAlternatives(data);
            });
    };
    return (
        <div style={{ width: "100%" }}>
            <Alternatives alternatives={alternatives} open={alternatives} setAlternatives={setAlternatives} />
            <Typography gutterBottom variant={"h1"}>
                {props.fullName}

                <IconButton style={{ marginLeft: 12 }} onClick={goBack}>
                    <EditIcon />
                </IconButton>
            </Typography>
            <Tabs
                value={currentTab}
                onChange={handleTabChange}
                indicatorColor="primary"
                textColor="primary"
                variant="scrollable"
                scrollButtons="auto">
                {WEEK_DAYS.map((day, index) => <Tab key={`day-tab-${index}`} label={day} />)}
            </Tabs>
            <SwipeableViews
                index={currentTab}
                onChangeIndex={(index) => setCurrentTab(index)}>
                {
                    (data && data.length > 0) ? data.map((dayGroup, index) => (
                        <TabContent key={`key-tabContent-${index}`} currentTab={currentTab} index={index}>
                            {
                                dayGroup.map(sesh => <Sesh onClick={() => fetchAlternatives({
                                    majorName: props.majorName,
                                    year: props.year,
                                    day: index + 1,
                                    time: sesh.time
                                })} key={`key-session-${sesh.id}`} {...sesh} />)
                            }
                        </TabContent>
                    )) :
                        <Typography variant="h5">loading... â˜•</Typography>
                }
            </SwipeableViews>
        </div>
    );
}

function TabContent(props) {
    const { currentTab, index, children } = props;
    return currentTab === index && children
}


TabContent.defaultProps = {
    currentTab: 0,
    index: 0,
}

export default withRouter(Schedule);
