import React from 'react';
import {Container} from "@material-ui/core";
import {makeStyles} from "@material-ui/styles";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import {Switch, Route} from "react-router-dom"
import MajorSelect from "./MajorSelect";
import SchedulePage from "./SchedulePage";
import Box from "@material-ui/core/Box";

const useStyle = makeStyles(theme => ({
    container: {
        overflowX: "hidden",
        height: "100vh",
        overflow: "visible",
        position: "relative",
        padding: theme.spacing(2),
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignItems: "flex-start"
    },

    formContainer: {
        border: "1px solid lightgray",
        padding: theme.spacing(2)
    },
    footer: {
        // bottom: theme.spacing(3)
    },
    twitterIcon: {
        width: 24,
        verticalAlign: "middle",
        marginRight: theme.spacing(1)
    },
    socialButtons: {
        textTransform: "lowercase"
    }
}));


function App() {

    const classes = useStyle();
    const _openSocialNetwork = (url) => {
        window.open(url, "_blank");
    };


    return (
        <Container className={classes.container} maxWidth={"md"}>
            <Switch>
                <Route exact path={"/"} component={MajorSelect}/>
                <Route exact path={"/:majorId"} component={SchedulePage}/>
            </Switch>
            <Box style={{flex: 1}}/>
            <footer className={classes.footer}>
                <Grid spacing={2} container>

                    <Grid item>
                        <Button className={classes.socialButtons}
                                onClick={() => _openSocialNetwork("https://twitter.com/haythem_farhat")}>
                            <img alt="twitter" className={classes.twitterIcon}
                                 src={"https://upload.wikimedia.org/wikipedia/fr/c/c8/Twitter_Bird.svg"}/>
                            @haythem_farhat
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button className={classes.socialButtons}
                                onClick={() => _openSocialNetwork("https://github.com/farhat-h")}>
                            <img alt="github" className={classes.twitterIcon}
                                 src={"https://image.flaticon.com/icons/svg/25/25231.svg"}/>
                            farhat-h
                        </Button>
                    </Grid>
                </Grid>
            </footer>
        </Container>
    );
}

export default App;
