import React, { useEffect, useState } from 'react';
import Typography from "@material-ui/core/Typography";
import Autocomplete from "@material-ui/lab/Autocomplete/Autocomplete";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/styles";
import useStoredValue from "./useStoredValue";
import { MAJOR_KEY } from "./storageKeys"

const useStyles = makeStyles(theme => ({
    input: {
        width: 320,
        maxWidth: "100%"
    },
    title: {
        marginTop: "25%"
    },

}));

function MajorSelect(props) {

    const [majorList, setMajorList] = useState([]);
    const [major, setMajor] = useStoredValue({ localStorageKey: MAJOR_KEY, isJSON: true });
    useEffect(() => {
        fetch("/api/majors").then(res => res.json()).then(_majorList => setMajorList(_majorList))
    }, []);
    useEffect(() => {
        if (major !== null)
            props.history.push(`/${major.majorId}`)
    }, [major]);
    const classes = useStyles();
    return (
        <React.Fragment>
            <Typography className={classes.title} gutterBottom align={"left"} variant={"h1"}>
                Pick your major
            </Typography>

            <Autocomplete
                onKeyDown={(e) => {
                    if (e.key === "Enter")
                        console.log("shit")
                }}
                defaultValue={major}
                onChange={(e, value) => {
                    setMajor(value)
                }}
                getOptionLabel={option => option.fullName}
                options={majorList}
                renderInput={
                    params =>
                        <TextField
                            className={classes.input}
                            {...params}
                            style={{ fontSize: "2rem" }}
                            margin={"normal"}
                            label={"MAJOR"}
                            placeholder={"ex: FIA1-01"} />}
            />
        </React.Fragment>
    );
}

export default MajorSelect;