import React, { useState, useEffect } from 'react';
import useStoredValue from "./useStoredValue";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import Button from "@material-ui/core/Button";
import { SCHEDULE_KEY as localStorageKey } from "./storageKeys"
import Schedule from "./Schedule";


const thinkingEmojiURL =
  "https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/google/223/thinking-face_1f914.png";
const useStyle = makeStyles(theme => ({
  errorContainer: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    height: "100vh",
    alignItems: "flex-start"
  },
  errorTitle: {
    marginBottom: theme.spacing(2)
  },
}));


function SchedulePage(props) {
  const classes = useStyle();
  const [schedule, setSchedule] = useStoredValue({ localStorageKey, isJSON: true });
  const [error, setError] = useState(null);

  useEffect(() => {
    const { majorId } = props.match.params;
    fetch(`/api/sessions/${majorId}`).then(res => res.json()).then(data => {
      if (data.error) {
        setError(data.error);
      } else {
        setSchedule(data)
      }
    });
  }, []);

  return (
    error ?
      <div className={classes.errorContainer}>
        <img
          align={"center"}
          src={thinkingEmojiURL} />
        <Typography gutterBottom={true} align={"left"} color={"error"} variant={"h1"}>{error}</Typography>
        <Button variant={"outlined"} color={"primary"} onClick={() => props.history.push("/")}>
          try to select another one
                </Button>
      </div> :
      <Schedule {...schedule} />
  )
}


export default SchedulePage;
