import React from 'react';
import List from "@material-ui/core/List";
import ListSubheader from "@material-ui/core/ListSubheader";
import { Typography } from "@material-ui/core";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";

function AlternativesList({ alternatives }) {
    const subjectNameExtractor = /-(.*?)-/;
    return (
        <List>
            {alternatives && alternatives.map((alt, index) => {
                const sesh = alt.Sessions[0];
                let rawSubjectName = subjectNameExtractor.exec(sesh.subject)[1];
                rawSubjectName = rawSubjectName.replace(/(\(.*\))/, "");

                return (
                    <React.Fragment key={`alternative-${index}`}>
                        {/* <ListSubheader>{alt.fullName}</ListSubheader> */}
                        <Typography style={{ marginTop: 12 }} variant="h6">{alt.fullName}</Typography>
                        <ListItem divider={true}>
                            <ListItemText
                                primary={rawSubjectName}
                                secondary={`${sesh.type} - ${sesh.room} - ${sesh.regime}`}
                            />
                        </ListItem>
                    </React.Fragment>
                )
            })}
        </List>
    );
}

export default AlternativesList;