import {useState, useEffect} from "react";

export default function (options = {localStorageKey: "", initialValue: null, isJSON: false}) {
    let storedValue = window.localStorage.getItem(options.localStorageKey) || null;
    if (storedValue && options.isJSON)
        storedValue = JSON.parse(storedValue);
    const [value, setValue] = useState(storedValue);
    useEffect(() => {
        if (value !== null)
            window.localStorage.setItem(options.localStorageKey, options.isJSON ? JSON.stringify(value) : value);
    }, [value]);
    return [value, setValue];

}